import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useLocation, Link } from "react-router-dom";
import HomePost from "../../components/HomePost";
import { getPosts } from "../../redux/reducers/postsReducer";
import { useDispatch, useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowUp } from "@fortawesome/free-solid-svg-icons";

import frontPageIcon01 from "../../static/frontpage_icon-01.png";
import frontPageIcon02 from "../../static/frontpage_icon-02.png";
import frontPageIcon03 from "../../static/frontpage_icon-03.png";
import taiwanMap from "../../static/map/taiwan.png";
import newtaipei from "../../static/map/newtaipei.png";
import changhua from "../../static/map/changhua.png";
import chiayi from "../../static/map/chiayi.png";
import hsinchu from "../../static/map/hsinchu.png";
import kaohsiung from "../../static/map/kaohsiung.png";
import miaoli from "../../static/map/miaoli.png";
import pingtung from "../../static/map/pingtung.png";
import taichung from "../../static/map/taichung.png";
import tainan from "../../static/map/tainan.png";
import taitung from "../../static/map/taitung.png";
import taoyuan from "../../static/map/taoyuan.png";
import yilan from "../../static/map/yilan.png";
import nantou from "../../static/map/nantou.png";
import taipei from "../../static/map/taipei.png";
import yunlin from "../../static/map/yunlin.png";
import hualien from "../../static/map/hualien.png";

import {
  MEDIA_QUERY_LG,
  MEDIA_QUERY_SM,
  MEDIA_QUERY_MD,
} from "../../constants/break_point";

const Wrapper = styled.div`
  position: relative;
  width: 75vw;
  margin: 0 auto;
  margin-top: 100px;
`;

const Heading = styled.div`
  position: absolute;
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 40px;
  margin-bottom: 20px;
  padding: 5px 20px;
  text-align: center;
  background: white;

  ${MEDIA_QUERY_SM} {
    display: block;
    text-align: center;
  }

  & h3 {
    font-weight: bolder;
    font-size: ${(props) => props.theme.titles.h5};
    color: ${(props) => props.theme.secondaryColors.secondaryDarker};

    ${MEDIA_QUERY_SM} {
      font-size: ${(props) => props.theme.fontSizes.medium};
    }
  }
`;

const AreaHeading = styled(Heading)`
  position: relative;
  top: 0px;
  background: transparent;

  & h3 {
    color: white;
  }
`;

const BannerContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 60px;
  padding: 50px;
  border: 2px solid ${(props) => props.theme.secondaryColors.secondary};
  background: white;

  ${MEDIA_QUERY_SM} {
    flex-direction: column;
  }
`;

const PostsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  ${MEDIA_QUERY_MD} {
    justify-content: center;
  }
`;

const IntroContainer = styled.div`
  width: 300px;
  display: flex;
  flex-direction: column;

  & + & {
    margin-left: 5px;
  }

  ${MEDIA_QUERY_LG} {
    width: 240px;
  }

  ${MEDIA_QUERY_MD} {
    width: 200px;
  }

  ${MEDIA_QUERY_SM} {
    & + & {
      margin-top: 10px;
    }
  }
`;

const IntroImage = styled.div`
  height: 260px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  ${(props) =>
    props.$01 &&
    `
    background-image: url(${frontPageIcon01});
  `}

  ${(props) =>
    props.$02 &&
    `
    background-image: url(${frontPageIcon02});
  `}

  ${(props) =>
    props.$03 &&
    `
    background-image: url(${frontPageIcon03});
  `}

  ${MEDIA_QUERY_LG} {
    height: 200px;
  }

  ${MEDIA_QUERY_MD} {
    height: 140px;
  }
`;

const IntroTitle = styled.div`
  width: 120px;
  margin: 10px auto 0px;
  font-size: ${(props) => props.theme.titles.h6};
  text-align: center;
  font-weight: bold;
  color: ${(props) => props.theme.primaryColors.primaryDark};

  ${MEDIA_QUERY_MD} {
    font-size: ${(props) => props.theme.fontSizes.medium};
  }
`;

const IntroContent = styled.div`
  min-height: 120px;
  padding: 15px 20px;
  word-break: break-all;
  text-align: justify;
  font-weight: bold;
  color: ${(props) => props.theme.secondaryColors.secondaryDarker};

  ${MEDIA_QUERY_MD} {
    padding: 5px 20px;
    min-height: 100px;
    font-size: ${(props) => props.theme.fontSizes.small};
    line-height: ${(props) => props.theme.fontSizes.medium};
  }
`;

const ExploreArea = styled.div`
  position: relative;
  z-index: -1;
  padding: 20px 40px 60px;
  background: ${(props) => props.theme.secondaryColors.secondary};

  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 125px;
    top: -120px;
    left: 0;
    background: ${(props) => props.theme.secondaryColors.secondary};
  }
`;

const MoreTag = styled(Link)`
  display: block;
  margin: 30px auto;
  padding: 2px 5px;
  width: 100%;
  border-radius: 5px;
  font-size: ${(props) => props.theme.fontSizes.small};
  border: 1px solid ${(props) => props.theme.secondaryColors.secondary};
  color: ${(props) => props.theme.secondaryColors.secondaryDarker};
  text-align: center;
  font-weight: bold;
  background: ${(props) => props.theme.secondaryColors.secondaryLight};
  cursor: pointer;
  transition: color 0.5s ease, background 1s ease;

  &:hover {
    box-shadow: 0 1px 2px grey;
    color: ${(props) => props.theme.secondaryColors.secondaryLighter};
    background: ${(props) => props.theme.secondaryColors.secondaryDarker};
  }
`;

const MapImageWrapper = styled.div`
  min-width: 280px;
  height: 540px;
  position: relative;
  overflow: hidden;

  ${MEDIA_QUERY_MD} {
    display: none;
  }
`;

const TaiwanImage = styled.img`
  position: absolute;
  top: -5px;
  right: 0;
  margin-right: -5%;
  width: 320px;

  ${MEDIA_QUERY_SM} {
    width: 300px;
  }
`;

const GoTopButton = styled.button`
  position: fixed;
  z-index: 5;
  bottom: 50px;
  right: 50px;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  background: ${(props) => props.theme.primaryColors.primaryDark};
  box-shadow: 2px 2px 2px grey;
  color: ${(props) => props.theme.basicColors.white};
  font-size: ${(props) => props.theme.fontSizes.large};
  transition: all 0.5s ease;

  &:hover {
    bottom: 52px;
    box-shadow: 2px 2px 5px grey;
  }
`;

const ExploreSection = styled.div`
  position: relative;
  width: 75vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;

  ${MEDIA_QUERY_SM} {
    width: 100%;
  }
`;

const PostsArea = styled.div`
  margin-right: 20px;
  flex: 1;

  ${MEDIA_QUERY_MD} {
    margin-right: 0;
  }
`;

const MapInfo = styled.div`
  position: absolute;
  top: 0px;
  z-index: 3;
`;

const MapLocation = styled.div`
  border-left: 5px solid ${(props) => props.theme.primaryColors.primaryLighter};
  margin-bottom: 10px;
  padding-left: 5px;
  height: 24px;
  line-height: 24px;
  font-size: ${(props) => props.theme.fontSizes.medium};
  color: ${(props) => props.theme.primaryColors.primaryLighter};
  font-weight: bold;
`;

const MapDescription = styled.div`
  width: 160px;
  padding: 5px;
  color: ${(props) => props.theme.secondaryColors.secondaryDarker};
  background: #f8dfc299;
  text-align: justify;
`;

export default function HomePage() {
  const location = useLocation();
  const dispatch = useDispatch();
  const postsData = useSelector((store) => store.posts.posts);
  const [areaHoverAt, setAreaHoverAt] = useState();
  const [goTopButtonShow, setGoTopButtonShow] = useState(false);

  const keywords = [
    [
      "",
      "taipei",
      taipei,
      "",
    ],
    [
      "",
      "newtaipei",
      newtaipei,
      "120",
    ],
    [
      "",
      "taoyuan",
      taoyuan,
      "",
    ],
    [
      "",
      "hsinchu",
      hsinchu,
      "",
    ],
    [
      "",
      "miaoli",
      miaoli,
      "650",
    ],
    [
      "",
      "taichung",
      taichung,
      "",
    ],
    [
      "",
      "changhua",
      changhua,
      "",
    ],
    [
      "",
      "yunlin",
      yunlin,
      "",
    ],
    [
      "",
      "chiayi",
      chiayi,
      "",
    ],
    [
      "",
      "tainan",
      tainan,
      "",
    ],
    [
      "",
      "kaohsiung",
      kaohsiung,
      "",
    ],
    [
      "",
      "pingtung",
      pingtung,
      "",
    ],
    [
      "",
      "taitung",
      taitung,
      "",
    ],
    [
      "",
      "hualien",
      hualien,
      "",
    ],
    [
      "",
      "yilan",
      yilan,
      " 50 ",
    ],
    [
      "",
      "nantou",
      nantou,
      "",
    ],
  ];

  useEffect(() => {
    dispatch(getPosts());
  }, [dispatch]);

  window.onscroll = function () {
    if (
      document.body.scrollTop > 20 ||
      document.documentElement.scrollTop > 20
    ) {
      setGoTopButtonShow(true);
    } else {
      setGoTopButtonShow(false);
    }
  };

  function handleTopButtonClick() {
    window.scrollTo({
      top: 10,
      left: 0,
      behavior: "smooth",
    });
  }

  return (
    <>
      <Wrapper $atHomepage={location.pathname === "/"}>
        <Heading>
          <h3></h3>
          <div></div>
        </Heading>
        <BannerContainer>
          <IntroContainer>
            <IntroImage $01={true}></IntroImage>
            <IntroTitle>planning</IntroTitle>
            <IntroContent>
              
              Hittheroad
            </IntroContent>
          </IntroContainer>
          <IntroContainer>
            <IntroImage $02={true}></IntroImage>
            <IntroTitle>Post-it</IntroTitle>
            <IntroContent>
               Google Maps 
            </IntroContent>
          </IntroContainer>
          <IntroContainer>
            <IntroImage $03={true}></IntroImage>
            <IntroTitle>album</IntroTitle>
            <IntroContent>
              
            </IntroContent>
          </IntroContainer>
        </BannerContainer>
      </Wrapper>
      <ExploreArea>
        <AreaHeading>
          <h3></h3>
        </AreaHeading>
        <ExploreSection>
          <PostsArea>
            {postsData && (
              <PostsContainer>
                {postsData.slice(0, 4).map((post, index) => (
                  <HomePost
                    postData={post}
                    index={index}
                    key={index}
                    setAreaHoverAt={setAreaHoverAt}
                  />
                ))}
              </PostsContainer>
            )}
            <MoreTag to={"/explore/location/"}>more</MoreTag>
          </PostsArea>
          <MapImageWrapper>
            {areaHoverAt && (
              <MapInfo>
                <MapLocation>{areaHoverAt}</MapLocation>
                <MapDescription>
                  {keywords.find((keyword) => keyword[0] === areaHoverAt)[3]}
                </MapDescription>
              </MapInfo>
            )}

            {keywords.map((keyword) => (
              <img
                key={keyword[1]}
                src={keyword[2]}
                alt={keyword[1]}
                width="100%"
                style={{
                  display: areaHoverAt === `${keyword[0]}` ? "block" : "none",
                  position: "absolute",
                  top: "-5px",
                  right: 0,
                  zIndex: "1",
                  marginRight: "-5%",
                  width: "320px",
                }}
              />
            ))}
            <TaiwanImage src={taiwanMap} alt="taiwan map" width="100%" />
          </MapImageWrapper>
        </ExploreSection>
      </ExploreArea>

      {goTopButtonShow && (
        <GoTopButton onClick={handleTopButtonClick}>
          <FontAwesomeIcon icon={faArrowUp} />
        </GoTopButton>
      )}
    </>
  );
}
